import React from 'react';
import './App.css';
import "./Components/Components.css"
import Routing from './Components/Routing';
import Settings from './Components/SettingsPage';
import IntroductionContainer from './Components/IntroductionContainer';
import TestContainer from './Components/TestContainer';
import Auth from './Components/Auth';

function App() {

    const [loc, setLoc] = React.useState<string>("");
    const [auth, setAuth] = React.useState<boolean>(localStorage.getItem("auth")=="true");
    React.useEffect(() => {
        const queryParameters = new URLSearchParams(window.location.search)
        setLoc(queryParameters.get("section") || "")

        if (queryParameters.get("section") == null ){
            window.location.href = "/mist-online/?section="
        }
    }, )
    

    return (
    <div className="App">
        build: 0.1.3
        {!auth? 
        <Auth cb={setAuth}/>
        :
        <>
        {loc=="" && <Routing/>}
        {loc=='settings' && <Settings/>}
        {loc=='introduction' &&  <IntroductionContainer/>}
        {loc=='test' &&  <TestContainer/>}
        </>
        }
    </div>
    );
}

export default App;
