import { PasswordInput, Card, Button } from '@mantine/core'
import React from 'react'

type Props = {
    cb: Function
}

export default function Auth({cb}: Props) {

    const [curr, setCurr] = React.useState("")
          return (
    <div>        
        <Card radius="lg" withBorder className='home-module' style={{marginTop:"10vh", }}
        >   <>
            <PasswordInput 
                value={curr} label="Access Code" withAsterisk
                onChange={(event) => setCurr(event.currentTarget.value)}
                size="sm"
            /> 
            <Button
                onClick={()=>{
                    if (curr=="2023"){
                        cb(true);
                        localStorage.setItem("auth","true")
                        window.location.href=window.location.href="/mist-online/?section="
                    }
                }}
            >
                Enter
            </Button>
            </>
        </Card>
</div>
  )
}