import React from 'react'
import { DefaultMantineColor, Progress } from '@mantine/core'
type Props = {curr: number}

export default function SuccessBar({curr}: Props) {

    const [sr, setSR] = React.useState(curr)
    const [sections, setSections] = React.useState<any>()

    React.useEffect(() => {
        setSR(curr)

        if (curr < 60){
            setSections([
                { value: curr, color:"red", label:Math.floor(curr).toString()+"%"},
                { value: 60-curr, color: 'cyan',  label:"60%"},
            ])
        }else{
            setSections([
                { value: 60, color: 'cyan',  label:"60%"},
                { value: curr-60, color:"green", label:Math.floor(curr).toString()+"%"},
            ])
        }
    }, [curr])


  return (
    <>
        <p>Accuracy: {curr.toFixed(1)}%</p>
        <Progress       
            size={'lg'}  
            style={{height:"3vh"}}
            sections={sections}
        />
    </>
  )
}