import React from 'react'
import { Modal, Group } from '@mantine/core'
import "./Components.css"
import { X, Check } from 'tabler-icons-react';
import correctsfx from '../Assets/correctsfx.mp3'
import errorsfx from '../Assets/errsfx.mp3'

type Props = {
    show: boolean,
    setShow: Function,
    verbose: boolean | null,
    result: string,
}

export default function ResultModal({show, setShow, verbose=false, result}: Props) {

    const [seconds, setSeconds] = React.useState(2);
    const [start, setStart] = React.useState(Date.now())
    const [color, setColor] =  React.useState("red")

    const setTime = () => {
        console.log(seconds)
        if (seconds > 0 && show){
            const time = Date.now() - start;
            const s = 1.2 - Math.floor((time / 1000) % 60);
            console.log(s)
            setSeconds(s);
            return s;
        }
        return 0;
    };


    React.useEffect(() => {

        if (result == "correct"){
            setColor("green")
            let audio = new Audio(correctsfx);
            audio.volume = 0.7
            audio.load();
            audio.play();
        }else{
            setColor("red")
            let audio = new Audio(errorsfx);
            audio.load();
            audio.play();
        }

        const interval = setInterval(() => {
            const s = setTime(); 
            console.log(s)
            if (s <= 0){
                setShow(false);
                clearInterval(interval)
            }
        }, 1000);

        return () => clearInterval(interval);
    }, [result]);
    
    return (
        <Modal 
            opened={show} 
            onClose={() => setShow(false)} closeButtonLabel='' 
            centered
            style={{backgroundColor:"transparent"}}
            shadow='none'
            withCloseButton={false}>
                <Group position='center' className={result!=="correct"?'a-modal':""}>
                    <div className='r-modal'>
                        <div style={{width:"100%", display:"flex", flexDirection:"row", justifyContent:"center"}}>
                        { result == "correct"?
                            <Check
                                size={96}
                                strokeWidth={2}
                                color={'green'}
                            />
                        :
                            <X
                                size={48}
                                strokeWidth={2}
                                color={color}
                                className='err-icon'
                            />
                        }   
                        </div>
                        <div style={{color:color,
                            width:"100%", display:"flex", flexDirection:"row", justifyContent:"center"}}>

                        <h2 style={{color:color, marginTop:'5vh'}}>
                            {result.toUpperCase()}
                        </h2>
                        </div>
                    </div>
                </Group>
        </Modal>

    )
}