import { Button, Card, Progress, Text } from '@mantine/core'
import React, { useEffect } from 'react'
import "./Components.css"
import ResultModal from './ResultModal';
import MDRenderer from './MDRenderer';


type Props = {
   
}

export default function IntroductionContainer({}: Props) {

    const DURATION = Number(localStorage.getItem("iDuration"));

    const [seconds, setSeconds] = React.useState(0); // time spent on current question
    const [totalSeconds, setTotalSeconds] = React.useState(0); // time spent on section
    const [itr, setItr] = React.useState<number>(0)
    const [showModal ,setShowModal] = React.useState<boolean>(false)
    const [equations, setEquations] = React.useState<Array<string>>([])
    const [answers, setAnswers] = React.useState<Array<number>>([])
    const [startIntro, setStartIntro] = React.useState<boolean>(false);
    const [times, setTimes] = React.useState<Array<number>>([])
    const [numEqs, setNumEqs] = React.useState<number>(0)
    const [loaded, setLoaded] = React.useState(false)
    const [startTime, setStartTime] = React.useState(Date.now())
    const [og_startTime, og_setStartTime] = React.useState(Date.now())
    const btnRef = React.useRef<HTMLInputElement>(null);
    const [currSelect, setCurrSelect] = React.useState(0)
    const [stopFlag, setStopFlag] = React.useState(false);
    const [cbTime, setCBTime] = React.useState(0)
    const [modalMessage, setModalMessage] = React.useState<string>("")

    const loadEquations = () => {
        
        setLoaded(true);
        console.log(DURATION)
        let queryString = new URLSearchParams({
            "section": "section i"
		})

        fetch(('https://mist-online-api.herokuapp.com/get_equations?' + queryString), {
          })
          .then(response => response.json())
          .then(response => {
                console.log(response)
              setEquations(response.equations)
              setAnswers(response.answers)
              setNumEqs(DURATION)
          })

    }

    const setShow = (s:boolean) =>{
        setShowModal(s);
        setStartTime(Date.now())
    }
  
    const handleSubmit = (solution:number) => {

        if (solution === answers[itr]){
            setModalMessage("correct")
        }else{
            setModalMessage("incorrect")
        }
        
        const sum = times.reduce((a, b) => a + b, 0);
        let avg = sum / itr;
        localStorage.setItem("average", avg.toString())
        console.log("Average:" ,avg)
        setShowModal(true)
        setItr(itr+1)

        
        setTimes([...times, seconds])
        console.log(times)
        setSeconds(0);
        setStartTime(Date.now());

        console.log(itr, numEqs)
        if(itr >= numEqs - 1 || stopFlag == true){
            setCBTime(Date.now())
        }


    }


    const setTime = (diff:number) => {

        const time = Date.now() - diff;
        const s =  (time / 1000) % 60;

        if(!showModal) setSeconds(s)

        const og_s = (Date.now()-og_startTime)/1000
        setTotalSeconds(og_s) 

        if(og_s >= DURATION && startIntro && !showModal){
            setStopFlag(true);
        }

        if(cbTime!==0){
            const temp = (Date.now() - cbTime)/1000
            console.log(temp)
            if (temp>1){
                window.location.href="/mist-online/?section=test"
            }

        }
        return s;
    };


    useEffect(() => {

        if (!loaded){
            loadEquations();
        }

        if (btnRef.current !== null){
            btnRef.current.focus();
        }

        const interval = setInterval(() => {

            const s = setTime(startTime);
            if (btnRef.current!==null) {
                btnRef.current.focus()
            }
            
        }, 100);

        return () => clearInterval(interval);

    }, [startTime]);

    
    const handleStart = (e:React.KeyboardEvent<HTMLInputElement>) =>{

        if(e.key=="Enter"){
            setStartIntro(true)
            og_setStartTime(Date.now());
            setStartTime(Date.now());
            setSeconds(0);
        }

        if (btnRef.current !== null){
            btnRef.current.focus();
        }
    }

    const handleSelect = (e:React.KeyboardEvent<HTMLInputElement>) => {

        if(showModal){return}

        if (e.key=="ArrowLeft"){
            setCurrSelect(Math.max(0, currSelect-1))
        }

        if (e.key=="ArrowRight"){
            setCurrSelect(Math.min(9, currSelect+1))
        }

        if (e.key=="Enter"){
            handleSubmit(currSelect);
            setCurrSelect(0)
        }
    }

    
    return (
        <>
            <h1>Introduction</h1>
            <Card radius="lg" withBorder className='test-module'   
                onClick={()=>{if (btnRef.current !== null){btnRef.current.focus()}}}
            >

                {!startIntro ? 
    
                    <div className='organize-row'>
                        <Button 
                            color={"blue"} style={{width:"50%"}}
                            id={"startIntro-button"}
                        >
                            Press Enter/Return to Start
                        </Button>
                        
                    </div>
                :  
                    <>  
                        <MDRenderer doc={ !showModal? equations[itr]: " "}/>
                        <div className='organize-row'>
                            {
                                Array.from(Array(10).keys()).map((solution)=>
                                <button 
                                    className='solution-card'
                                    style={solution === currSelect ? {borderColor:"rgb(162,162,162"}: {}}
                                >
                                    {solution}
                                </button>
                                )
                            }
                        </div>
                    </>
                    
                }
                {showModal &&
                    <ResultModal show={showModal} setShow={setShow} result={modalMessage} verbose={false}/>
                }           
            </Card>


            <input style={{width:"0px", zIndex:"-100"}} ref={btnRef} 
                onKeyUpCapture={(e)=>{!startIntro? handleStart(e): handleSelect(e)}}>
            </input>
        </>
    );
}