import React from 'react'
import { Card, TextInput, Button } from '@mantine/core'
import "./Components.css"
type Props = {}

export default function Settings({}: Props) {

    const [iDuration, setIDuration] = React.useState<number|null>();
    const [tDuration, setTDuration] = React.useState<number|null>();
    const [numSections, setNumSections] = React.useState<number|null>();
    const defaultLimit = 3.5;
    const [limits, setLimits] = React.useState<Array<number>>([3.6,3.6,3.6,3.6])

    React.useEffect(() => {
        fetch(('https://mist-online-api.herokuapp.com/get_settings?' ), {
        })
        .then(response => response.json())
        .then(response => {
            setIDuration(response.iDuration)
            setTDuration(response.tDuration)
            setNumSections(response.numSections)
            setLimits(response.limits)
        })
    }, [])
    
    const set = (val:string, setter:Function) => {

        console.log(val)
        console.log(val[val.length-1])
        if(!isNaN(Number(val))){
            setter(Number(val));
            if (setter == setNumSections){
                handleLimits(Number(val));
            }
        }
    }

    const handleLimits = (num:number) => {
        setLimits(Array(num).fill(3.5))
    }

    const handleLimitChange = (val:string, i:number) => {
        if(!isNaN(Number(val)) || val=="."){
            const newArray = [...limits]
            newArray[i] = Number(val);
            setLimits(newArray)
        }
    }

    const saveSettings = () => {
        // sending data
		fetch('https://mist-online-api.herokuapp.com/set_settings?', {
			method: 'POST',
			headers: { 'Content-Type': 'application/json' },
			body: JSON.stringify({ 
                iDuration: iDuration,
                tDuration: tDuration,
                numSections: numSections,
                limits: limits,
			})
		})
			.then((response) => response.json())
			.then((data) => {
            })

    }
    const begin = () => {

        if (iDuration && tDuration && numSections && limits){
        localStorage.setItem("iDuration", iDuration.toString());
        localStorage.setItem("tDuration", tDuration.toString());
        localStorage.setItem("numSections", numSections.toString());
        localStorage.setItem("limitsArray", JSON.stringify(limits));
        }

        saveSettings();


        window.location.href="/mist-online/?section=introduction"
    }
    
    return (
        <div>
                <Card radius="lg" withBorder className='settings-module' style={{marginTop:"10vh"}}>
                    <h3>Settings</h3>
                    <div style={{"textAlign":"left", padding:"5vh"}}>
                    <TextInput 
                        value={iDuration || ""} label="Introduction Section Duration"
                        onChange={(event) => set(event.currentTarget.value, setIDuration)}
                        rightSection="seconds" rightSectionWidth={"10vw"}
                        size="sm"
                    /> 
                    <TextInput 
                        value={tDuration || ""} label="Test Section Duration"
                        onChange={(event) => set(event.currentTarget.value, setTDuration)}
                        rightSection="seconds" rightSectionWidth={"10vw"}
                    />
                    <TextInput 
                        value={numSections || ""} label="Number of Test Sections"
                        onChange={(event) => set(event.currentTarget.value, setNumSections)} 
                    />

                    </div>
                    Minimum Time Limit for Each Questions by Section (Seconds)
                    <div className='organize-row-settings'>

                        {
                            Array.from(Array(numSections).keys()).map((i) =>
                                <TextInput style={{"width":"8vw"}}
                                label={i+1}
                                value={limits[i]||""}
                                onChange={(event) => handleLimitChange(event.currentTarget.value, i)} 
                                />
                            )
                        }
                    </div>

                    <Button style={{margin:"2vh"}} onClick={()=>saveSettings()}>Save Settings</Button>
                    <Button style={{margin:"2vh"}} onClick={()=>begin()}>Set and Begin MIST Protocol</Button>
                </Card>

        </div>
    )
}