import { Button, Card, Progress, Text } from '@mantine/core'
import React, { useEffect } from 'react'
import "./Components.css"
import ResultModal from './ResultModal';
import MDRenderer from './MDRenderer';
import SuccessBar from './SuccessBar';
import errorsfx from '../Assets/errsfx.mp3'
import correctsfx from '../Assets/correctsfx.mp3'

type Props = {
}

export default function TestContainer({ }: Props) {

    const DURATION = Number(localStorage.getItem("tDuration"));
    const numSections = Number(localStorage.getItem("numSections"));
    const minimumLimits:any = JSON.parse(localStorage.getItem("limitsArray") || "");
    const WAIT_DURATION = 6;

    const [seconds, setSeconds] = React.useState(0);

    const [totalSeconds, setTotalSeconds] = React.useState(0);
    const [sect, setSect] = React.useState(1);

    const [waitAdd, setWaitAdd] = React.useState(0);

    const [itr, setItr] = React.useState<number>(0)
    const [showModal, setShowModal] = React.useState<boolean>(false)
    const [equations, setEquations] = React.useState<Array<string>>([])
    const [answers, setAnswers] = React.useState<Array<number>>([])

    const [startTest, setStartTest] = React.useState<boolean>(false);

    const [times, setTimes] = React.useState<Array<number>>([])
    const [numEqs, setNumEqs] = React.useState<number>(0)
    const [loaded, setLoaded] = React.useState(false)
    const [startTime, setStartTime] = React.useState(Date.now())
    const [og_startTime, og_setStartTime] = React.useState(Date.now())
    const btnRef = React.useRef<HTMLInputElement>(null);
    const [limit, setLimit] = React.useState(10);
    const [currSelect, setCurrSelect] = React.useState(0)

    const [modalMessage, setModalMessage] = React.useState<string>("")
    const [sr, setSr] = React.useState<number>(1)

    const [flag, setFlag] = React.useState<boolean>(false)
    const [numCorrect, setNumCorrect] = React.useState<number>(0);
    const [numAttempts, setNumAttemps] = React.useState<number>(0);


    const loadEquations = (section:number) => {

        setLoaded(true);
        let queryString = new URLSearchParams({
            "section": "section " + section.toString()
		})

        console.log(minimumLimits)

        fetch(('https://mist-online-api.herokuapp.com/get_equations?' + queryString), {
          })
          .then(response => response.json())
          .then(response => {
              setEquations(response.equations)
              setAnswers(response.answers)
              setNumEqs(400)
          })

        // resets the limit for the new section
        switch(section){
            case 1:
                setLimit(Math.max(Number(localStorage.getItem("average"))-5,4))
                break

            case 2:
                setLimit(Math.max(Number(localStorage.getItem("average"))-1,5))
                break

            case 3:
                setLimit(Math.max(Number(localStorage.getItem("average"))-1,6))
                break

            case 4:
                setLimit(Math.max(Number(localStorage.getItem("average")),7))
                break
        }

    }

    const setShow = (s: boolean) => {
        setShowModal(s);
        setStartTime(Date.now())
    }


    const handleLimit = (c:number, a:number) => {

        let new_sr = c/a;
        setSr(new_sr);

        if (waitAdd > 0){
            if (sr> 0.6){
                setWaitAdd(waitAdd-3)
            }else if (sr> 0.52){
                setWaitAdd(waitAdd-2)
            }else{
                setWaitAdd(waitAdd-1);

            }
            return;
        }

        setWaitAdd(Math.floor(Math.random() * 5))

        // calculate SR
        let avg = Number(localStorage.getItem("average"))
        localStorage.setItem("average", avg.toString())

        console.log(new_sr)
        if (new_sr < 0.5) {
            let nlimit = limit+(Date.now()%10 /50 * sect)
            localStorage.setItem("average",nlimit.toString())
            setLimit(Math.max(nlimit,3.5))

        }else if(new_sr > 0.6){
            let nlimit = limit-(Date.now()%10 )/ 5;
            localStorage.setItem("average",nlimit.toString())
            setLimit(Math.max(nlimit,3.5))
        }
        else if(new_sr > 0.52){
            let nlimit = limit-(Date.now()%10 )/ 8;
            localStorage.setItem("average",nlimit.toString())
            setLimit(Math.max(nlimit,3.5))
        }


    }


    const handleSubmit = (solution: number) => {

        setItr(itr + 1);
        setTimes([...times, seconds]);
        setSeconds(0);
        setStartTime(Date.now());

        if (solution === answers[itr]) {
            setModalMessage("correct")
            handleLimit(numCorrect+1, numAttempts+1);
            setNumCorrect(numCorrect + 1);

        } else {
            handleLimit(numCorrect, numAttempts+1);


            if (solution == -1){
                setModalMessage("timeout! \n incorrect")

            }else{
                setModalMessage("incorrect")
            }
        }
        setShowModal(true)
        setNumAttemps(numAttempts+1);

        console.log("SUCCESSRATE:", sr)


        if (itr >= numEqs - 1 || flag==true) {
            incrementSection();
        }

    }


    const incrementSection = () => {

        setStartTest(false);
        if (sect == numSections) {

        }

        og_setStartTime(Date.now());
        setTotalSeconds(0);
        setSeconds(0);
        loadEquations(sect+1);
        setSect(sect + 1)
        setItr(0)
        setFlag(false);
    }


    const setTime = (diff: number) => {

        if (sect > 4) {
            return;
        }

        const time = Date.now() - diff;
        const s = (time / 1000) % 60;

        if (!showModal) setSeconds(s)

        const total_seconds = (Date.now() - og_startTime) / 1000
        setTotalSeconds(total_seconds)

        if (limit - s <= 0 && startTest==true) {
            handleSubmit(-1)
        }

        if (total_seconds > DURATION && startTest!=false) {
            setFlag(true)
        }

        return s;
    };


    useEffect(() => {

        // if the limit is NaN because the introduction section was skipped
        if (Number.isNaN(limit)){
            setLimit(5);
            localStorage.setItem("average", "5")
        }
        console.log(numSections)
        if (!loaded) { 
            loadEquations(1);
        }

        if (btnRef.current !== null) {
            btnRef.current.focus();
        }
        
        const interval = setInterval(() => {

            const s = setTime(startTime);
            if (btnRef.current !== null) {
                btnRef.current.focus()
            }
        }, 50);

        return () => clearInterval(interval);
    }, [startTime, sect, startTest, limit]);


    const handleStart = (e: React.KeyboardEvent<HTMLInputElement>) => {
        console.log(e.key)

        if (totalSeconds < WAIT_DURATION){
            return;
        }
        if (e.key == "Enter") {
            setStartTest(true)
            og_setStartTime(Date.now());
            setTotalSeconds(0)
            setStartTime(Date.now());
            setSeconds(0);
        }

        if (btnRef.current !== null) {
            btnRef.current.focus();
        }
    }

    const handleSelect = (e: React.KeyboardEvent<HTMLInputElement>) => {

        if (showModal) { return }

        if (e.key == "ArrowLeft") {
            setCurrSelect(Math.max(0, currSelect - 1))
        }

        if (e.key == "ArrowRight") {
            setCurrSelect(Math.min(9, currSelect + 1))
        }

        if (e.key == "Enter") {
            handleSubmit(currSelect);
            setCurrSelect(0)
        }
    }


    return (
    <>
        <h1>Section {sect}</h1>
        <Card radius="lg" withBorder className='test-module'

            onClick={() => {
                if (btnRef.current !== null) {
                    btnRef.current.focus();
                }
            }}
        >
            {sect <= numSections ?
                <>

                    {

                        !startTest ?

                            <div className='organize-row'>
                                <Button
                                    color={"blue"} style={{ width: "50%" }}
                                    id={"startTest-button"}
                                >
                                    Press Enter/Return to Start
                                </Button>

                            </div>
                            :
                            <>
                                {numAttempts>2 && <SuccessBar curr={sr * 100} />}
                                <p className={seconds * 2 > limit ? "blink": "" } style={{
                                    fontSize:"1.4rem",
                                    color: seconds * 2 > limit? "red": "black"
                                }}>
                                    {(limit - seconds).toFixed(1)} seconds remaining
                                </p>


                                {!showModal ? <MDRenderer doc={equations[itr]}/>
                                :
                                    <MDRenderer doc="  "/>
                                }
                    

                                <div className='organize-row'>

                                    {
                                        Array.from(Array(10).keys()).map((solution) =>
                                            <button
                                                className='solution-card'
                                                // onClick={() => { handleSubmit(solution) }}
                                                style={solution === currSelect ? { borderColor: "rgb(162,162,162" } : {}}
                                            >
                                                {solution}
                                            </button>
                                        )
                                    }
                                </div>
                            </>
                    }
                </>

                :
                <SuccessBar curr={sr * 100} />

            }
            {showModal &&
                <ResultModal show={showModal} setShow={setShow} result={modalMessage} verbose={true} />
            }

        </Card>

        <input style={{ width: "0px", zIndex: "-100" }} ref={btnRef}
            onKeyUpCapture={(e) => {
                !startTest ? handleStart(e) : handleSelect(e)
            }}>

        </input>
        </>

    );
}